<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryId" clearable @clear="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderById"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '0'" type="danger"></el-tag>
            <el-tag v-else type="sucess"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="showAddressDialog"></el-button>
            <el-button
              type="success"
              size="mini"
              icon="el-icon-location"
              @click="showProgressDialog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="pageSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="cascaderProps"
            clearable
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'

export default {
  name: '',
  components: {},

  data () {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      addressDialogVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [
          { required: true, message: '/' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityData,
      // 
      cascaderProps: {
        expandTrigger: 'hover'
      },
      // 
      progressDialogVisible: false,
      // 
      progressInfo: [],
      // id
      queryId: ''
    }
  },

  created () {
    this.getOrderList()
  },

  methods: {
    // 
    async getOrderList () {
      const { data: res } = await this.axios.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        // console.log(res.data)
        this.orderList = res.data.goods
        this.total = res.data.total
      }
    },
    // 
    pageSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showAddressDialog () {
      this.addressDialogVisible = true
    },
    // 
    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    async showProgressDialog () {
      const { data: res } = await this.axios.get('kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.progressInfo = res.data
      }
      this.progressDialogVisible = true
    },
    //  id 
    async getOrderById () {
      const { data: res } = await this.axios.get('orders/' + this.queryId)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        // console.log(res.data)
        this.orderList = []
        this.orderList.push(res.data)
      }
    }
  }
}

</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
